@import '../variables.scss';

.projectGrid *{
    list-style: none;
}

.project{
    margin-bottom: 40px;
}

.project h2{
    font-size: 1.5em;
}

.project ul{
    display: flex;
    margin-top: 5px;
}

.project li{
    margin-right: 15px;
}

.back{
    margin-left: 25px;
    margin-bottom: 20px;
    margin-top: 10px;
    font-size: 1.25em;

    @include breakpoint(mediumWidth){
        position: fixed;
        margin-left: 50px;
        margin-top: -25px;
    }

    @include breakpoint(desktopWidth){
        margin-top: -50px;
    }
}