.appContainer{
    position: relative
}

.home, .page{
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
}

.home-enter, .home-appear{
    .left{
        opacity: 0;
        transform: translateX(-10px);
    }
    
    .right{
        opacity: 0;
        transform: translateX(-5px);
    }
}

.home-enter-active, .home-appear-active {
    .left{
        opacity: 1;
        transform: translateX(0);
        transition: .25s ease-in-out;
        transition-property: opacity, transform;
        transition-delay: .45s;
    }

    .right{
        opacity: 1;
        transform: translateX(0);
        transition: .25s ease-in-out;
        transition-property: opacity, transform;
        transition-delay: .35s;
    }
}

.home-exit {
    .left{
        opacity: 1;
        transform: translateX(0);
    }

    .right{
        opacity: 1;
        transform: translateX(0);
    }
}

.home-exit-active {
    .left{
        opacity: 0;
        transform: translateX(-5px);
        transition: .25s;
        transition-property: opacity, transform;
    }
    
    .right{
        opacity: 0;
        transform: translateX(-10px);
        transition: .25s;
        transition-property: opacity, transform;
        transition-delay: .1s;
    }
}

.page-enter, .page-appear{
    .left{
        opacity: 0;
        transform: translateX(10px);
    }
    
    .right{
        opacity: 0;
        transform: translateX(5px);
    }

    .back{
        opacity: 0;
    }
}

.page-enter-active, .page-appear-active {
    .left{
        opacity: 1;
        transform: translateX(0);
        transition: .25s ease-in-out;
        transition-property: opacity, transform;
        transition-delay: .45s;
    }

    .right{
        opacity: 1;
        transform: translateX(0);
        transition: .25s ease-in-out;
        transition-property: opacity, transform;
        transition-delay: .35s;
    }

    .back{
        opacity: 1;
        transition: .1s ease-in-out;
        transition-property: opacity;
        transition-delay: .5s;
    }
}

.page-exit {
    .left{
        opacity: 1;
        transform: translateX(0);
    }

    .right{
        opacity: 1;
        transform: translateX(0);
    }

    .back{
        opacity: 1;
    }
}

.page-exit-active {
    .left{
        opacity: 0;
        transform: translateX(5px);
        transition: .25s;
        transition-property: opacity, transform;
    }
    
    .right{
        opacity: 0;
        transform: translateX(10px);
        transition: .25s;
        transition-property: opacity, transform;
        transition-delay: .1s;
    }

    .back{
        opacity: 0;
        transition: .1s ease-in-out;
        transition-property: opacity;
    }
}